#attendance {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
  padding: 3rem;
  height: 100vh;

}

.attendance-heading span span {
  font-size: 1.7rem;
  font-weight: 400;
}

#attendance-table-id {
  padding: 2rem 0;
}

.attendance-table-div {
  width: 100%;
  height: auto;
  background-color: var(--color-white);
  border-radius: 1rem;
}






















