#userprofile {
  position: relative;
  width: 4rem;
  height: 4rem;
  cursor: pointer;
}

#userprofile img {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

#user-profile-div {
  width: 20rem;
  height: 100%;
}

.profile-icons {
  width: 2rem;
  height: 2rem;
  color: var(--color-grey-3);
  cursor: pointer;
}
.profile-icons:hover {
  color: var(--color-text);
}
.notification {
  position: relative;
  width: 3.5rem;
  height: 3.5rem;
  background-color: var(--color-white);
  border-radius: 100%;
}

.notification-count {
  position: absolute;
  top: 0;
  left: 1rem;
  width: 1rem;
  height: 1rem;
  background-color: var(--color-text);
  border-radius: 50%;
  font-size: 0.7rem;
  text-align: center;
  color: var(--color-white);
  visibility: hidden;
}

.profile-setting {
  position: relative;
  width: 3.5rem;
  height: 3.5rem;
  background-color: var(--color-white);
  border-radius: 100%;
}

.username span {
  display: block;
  text-align: center;
}
